// import ContractType from "./contractType";
import * as variable from "../../variables/Variables";
import JSONPLACEHOLDERApi from "api/JSONPLACEHOLDERApi";
import {
  createPostData,
  convertDateToString,
  createAccountData,
  createHeader,
} from "util/ContructorCreation";
const { createSlice, createAsyncThunk } = require("@reduxjs/toolkit");


/**
 * * C mt cht khc bit khi ta to async action,
 * * thay v hi trc slice m ta to ra s cho ta 2 bin l reducer v actions th
 * * by h ta ko  actions tn ngn vy na m s export ra thng ci async action no cn lunlun
 */

/**
 * * tham s u s l param m ta mun pass vo ci hm ny
 * * ci tip theo l mt th m hm 'createAsyncThunk' cung cp cho php ta c dispatch nhng
 * * action  cc ni khc  dng trong hm ny: thunkAPI.dispatch()
 */
export const getALL = createAsyncThunk(
  "account/getFakeAPI",
  async (params, thunkAPI) => {
    const result = await JSONPLACEHOLDERApi.getAll(null);
    return result;
  }
);

export const sharingDetail = createAsyncThunk(
  "account/sharingDetail",
  async (params, thunkAPI) => {
    const result = await JSONPLACEHOLDERApi.shareDetail(params);
    return result;
  }
);

/**
 * * api tr v thng tin ca cy da trn tn
 */
export const getPlantInfo = createAsyncThunk(
  "account/getPlantInfo",
  async (params, thunkAPI) => {
    const result = await JSONPLACEHOLDERApi.getPlantInfo(params);
    return result;
  }
);
export const login = createAsyncThunk(
  "account/",
  async (params, thunkAPI) => {
    const result = await JSONPLACEHOLDERApi.getPlantInfo(params);
    return result;
  }
);


const account = createSlice({
  name: "account",
  initialState: {
    token:"",
    accountData: [
      //  createAccountData(1,"alive","Kh bnh","samxxx@gmail.com","08/17/1998","Nam","02/23/2021","08081501"),
      //  createAccountData(2,"dead","Khong bnh lm","samxxx@gmail.com","02/17/1990","Nam","02/23/2021","08081501"),
      //  createAccountData(3,"alive","Okiem ","samxxx@gmail.com","01/17/1991","N","02/23/2021","08081501"),
      createAccountData("1", "a live", "Kh bnh", "02/23/2021","12","samxxx@gmail.com","08/17/1998","Nam","090242342"),
      createAccountData("2", "dead", "Khong bnh lm", "02/23/2021","23","samxxx@gmail.com","02/17/1990","Nam","042342334"),
      createAccountData("3", "alive", "Okiem ", "02/23/2021","5","samxxx@gmail.com","01/17/1991","N","0774342234"),
    ],
    accountTableHeader: [
      createHeader("Ngi dng", false, true, variable.userName),
      createHeader("Ngy to", false, true, variable.createDate),
      createHeader("Email", false, true, variable.email),
      createHeader("Ngy sinh", false, true, variable.birthDate),
      createHeader("Gii tnh", false, true, variable.sex),
      createHeader("Trng thi", false, true, variable.accoutStatus),
      createHeader("S in thoi", true, false, variable.phone),
      createHeader("S lng bi b bo co", true, false, variable.numberOfReport),
    ],
    current: {},
    loading: false, // * trng ny dng  hin th pop up loading trong lc di API tr v response
    success: false, // * trng ny dng  hin th pop up success nu nh gi API thnh cng
    fail: false, //* trng ny dng  hin th pop up fail nu nh gi API tht bi
    error: "",
    signInForm: [
      {
        row: {
          cols: [
            {
              elementType: variable.input,
              elementConfig: {
                name: variable.username,
                type: variable.text,
                labeltext: "User name",
                placeholder: "Username",
                value: "",
              },
              validation: [],
              valid: {},
            },
          ],
        },
      },
      {
        row: {
          cols: [
            {
              elementType: variable.input,
              elementConfig: {
                name: variable.password,
                type: variable.password,
                labeltext: "",
                placeholder: "Password",
                value: "",
              },
              validation: [],
              valid: {},
            },
          ],
        },
      },
    ],
  },
  reducers: {
    addContract: (state, action) => {
      /**
       * ! reduxtoolkit  tch hp sn b immer cho php ta mutate state trc tip
       * *  trc giao din ta vit mutate trc tip nhng tht ra behind the sence
       * * immer vn s clone ra mt object mi v chnh sa ri return nh ta hay lm
       * *
       */
      state.contracts.record.push(action.payload);
    },
    modifyContract: (state, action) => {
      state.contracts.record = action.payload;
    },
    ModifyContractFomr: (state, action) => {
      state.contractForm = action.payload;
    },
  },

  // ! Vi kt qu tr v u s c 3 trng l meta,data v error, ta vit  di ny  coi s phi lm nhng g cho tng loi kt qu
  extraReducers: {
    [getALL.pending]: (state) => {
      state.loading = true;
      state.success = false;
      state.fail = false;
    },
    [getALL.rejected]: (state, action) => {
      // * Thc hin hnh ng g   store khi kt qu tr v khng c d liu nh mong mun
      state.error = action.error;
      state.loading = false;
      state.success = true;
      state.fail = false;
      state.current = action.payload;
    },
    [getALL.fulfilled]: (state, action) => {
      // * Thc hin hnh ng g d  store khi kt qu c d liu tr v thnh cng
      state.loading = false;
      state.success = true;
      state.fail = false;
      state.current = action.payload;
    },
    [getPlantInfo.pending]: (state) => {},
    [getPlantInfo.rejected]: (state, action) => {},
    [getPlantInfo.fulfilled]: (state, action) => {},
  },
});

const { reducer: accountReducer, actions } = account; //createSlice s tr v cho ta 2 bin l reducer v action
export const { addContract, modifyContract, ModifyContractFomr } = actions;
export default accountReducer;
