// api/axiosClient.js
import axios from "axios";
import queryString from "query-string";
import * as variable from "variables/Variables";
import { useDispatch, useSelector } from "react-redux";
import { getToken } from "redux/Selector/Selectors";
// Set up default config for http requests here

// Please have a look at here `https://github.com/axios/axios#request-
// config` for the full list of configs

const axiosClient = axios.create({
  baseURL: variable.jsonplaceholerAPI,
  headers: {
    "content-type": "application/json",
  },
  //   ! Vic parse param ca asiox c hi
  // !vn  nn ta s s dng query string  x l vic ny

  //? V d nh ta phi vit query stirng th ny : ?userid=""&password="". Kh l mt,
  //?dng ci th vi ny ta ch cn b param vo thi, cn li n s t chuyn thnh query trn
  paramsSerializer: (params) => queryString.stringify(params),
});
axiosClient.interceptors.request.use(async (config) => {
  return config;
});

// * mi reponse ta s phi thc hin ly
axiosClient.interceptors.response.use(
  (response) => {
    return response;
  },
  (error) => {
    throw error;
  }
);

