/**
 * * Axios dnh ring cho nhng api ko cn token
 */
export const axiosWithoutToken = axios.create({
  baseURL: variable.teamBaseURL,
  headers: {
    "Content-type": "application/json",
  },
  paramsSerializer: (params) => queryString.stringify(params),
});

// ! X l phn authen token, mi api  c ci ny mi
axiosWithoutToken.interceptors.request.use(async (config) => {
  return config;
});
axiosWithoutToken.interceptors.response.use(
  (response) => {
    return response;
  },
  (error) => {
    throw error;
  }
);

axiosTeamClient.interceptors.request.use(async (config) => {
  //* Phn token ta s ly  store sau khi user ng nhp thnh cng =)
  const token = useSelector((state) => getToken(state));
  config.headers = {
    Authorization: `Bearer ${token}`,
    // Accept: "application/json",
    // "Content-Type": "application/x-www-form-urlencoded",
  };
  return config;
  // },
  // (errorr) => {
  //   Promise.reject(errorr);
});

// * mi reponse ta s phi thc hin ly
axiosTeamClient.interceptors.response.use(
  //*reponse.data  ly v json yu cu, ta thc hin  y lun cho l
  // ! ngh li th ta cn nhiu th hn n thun l data
  (response) => {
    return response;
  },
  (error) => {
    // * Nu ta mun xem ci ny th  kt qu tr v ta .error th ta s thy r
    // * V nu mun xem status code th  .request cng s c 
    throw error;
  }
);

export default axiosClient;
